@import url("https://fonts.googleapis.com/css?family=Roboto");
body {
  margin: 0;
  padding: 0;
  font-family: "Roboto", sans-serif;
  box-sizing: border-box; }

a {
  text-decoration: none;
  color: inherit; }

.app {
  width: 100vw;
  height: 100vh;
  background-color: black;
  color: #5F5B6B; }

.homepage {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 16px; }
  .homepage__title {
    font-size: 100px;
    font-weight: bold; }
  .homepage__controls {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    margin-top: 20px;
    min-height: 200px; }

.button {
  padding: 8px 16px;
  border: 0;
  border-radius: 2px;
  background: #ffa726;
  color: white;
  font-size: 30px; }

.navigation {
  color: white;
  background: #182825; }
  .navigation__list {
    display: flex;
    justify-content: flex-end;
    margin: 0;
    list-style-type: none; }
    .navigation__list__item {
      margin: 20px;
      padding: 8px 16px;
      border: 0;
      border-radius: 2px;
      background: #7F7979; }



